//  entry
import Vue from 'vue'
import App from './App.vue'
import { createRouter } from './router'

// 
// router  store 
export function createApp () {
    const router = createRouter()
    const app = new Vue({
        //  Vue 
        router,
        // 
        render: h => h(App)
    })
    return { app, router }
}